import {request} from "../axios/request";

const baseUrl = '/api/admin'

const DashBoardApi = {}

DashBoardApi.get = () => {
  return request({
    url: `${baseUrl}/dashboard`,
    method: 'get'
  })
}

DashBoardApi.saveEssay = (content) => {
  return request({
    url: `${baseUrl}/SaveEssay`,
    method: 'post',
    params: {
      content
    }
  })
}

export default DashBoardApi